import React, { useEffect } from "react";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router";
import { logoutUser } from "../../actions/user";

function Logout() {
	const dispatch = useDispatch();
	const h = useHistory();

	useEffect(() => {
		dispatch(logoutUser());
		h.push("/");
	}, []);

	return <div></div>;
}

export default Logout;
